import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import { useAdmissionOngoingApplicationReview } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionReviewCard from "./AdmissionReviewCard";
import AdmissionReviewResuffleMenu from "./AdmissionReviewResuffleMenu";
import OneOngoingSearchWrapper from "../OneOngoingSearchWrapper";

const AdmissionReview = ({ applicationDetail, instituteId, aid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const params = useParams();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [resuffle, setResuffle] = useState(false);

  const {
    admissionOngoingApplicationReview,
    admissionOngoingApplicationReviewLoading,
    admissionOngoingApplicationReviewRefetch,
  } = useAdmissionOngoingApplicationReview({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationReview?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationReview?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationReview?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionOngoingApplicationReviewLoading &&
      admissionOngoingApplicationReview?.list?.length >= 0
    ) {
      if (e.target.value) {
        setSearchCopy(e.target.value);
      } else {
        setSearchCopy("");
        setSearch("");
      }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionOngoingApplicationReviewRefetch();
  }, [admissionOngoingApplicationReviewRefetch]);

  const onMultipleSelect = () => {
    let url =
      getQuery.state?.urlOffSet === "Institute"
        ? `/q/${params.username}/institute/member/admission?type=ongoing-detail&n=review-multi`
        : `/q/${params.username}/member/staff/admission?type=ongoing-detail&n=review-multi`;
    navigate(url, { state: getQuery.state });
  };

  const onResuffleToggle = useCallback(() => {
    setResuffle((pre) => !pre);
  }, []);
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
          isExportAccess="Confirm_Query"
          isTransferAccess="Review"
          aid={aid}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingSearchWrapper
            applicationDetail={applicationDetail}
            instituteId={instituteId}
            isExportAccess="Review_Query"
            isTransferAccess="Review"
            aid={aid}
            onMultipleSelect={onMultipleSelect}
            onResuffleToggle={onResuffleToggle}
          >
            <input
              type="text"
              placeholder={t("search_applicants")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingSearchWrapper>

          {admissionOngoingApplicationReview?.list?.map((applicant, index) =>
            admissionOngoingApplicationReview?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <AdmissionReviewCard
                  applicant={{ student: applicant }}
                  instituteId={instituteId}
                  onRefetch={onRefetch}
                  appId={getQuery.state?.appId}
                />
              </div>
            ) : (
              <AdmissionReviewCard
                applicant={{ student: applicant }}
                key={applicant?._id}
                instituteId={instituteId}
                onRefetch={onRefetch}
                appId={getQuery.state?.appId}
              />
            )
          )}
          {admissionOngoingApplicationReviewLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            admissionOngoingApplicationReview?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants found related this search."
              />
            )
          ) : (
            admissionOngoingApplicationReview?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {resuffle && (
        <AdmissionReviewResuffleMenu
          onClose={onResuffleToggle}
          appId={applicationDetail?._id}
          onRefetch={onRefetch}
        />
      )}
    </>
  );
};

export default AdmissionReview;
const isAuthorizedComplete = {
  isCompleted: true,
};
