import { useEffect, useState } from "react";
import { useAdmissionOngoingApplicationReviewBatch } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import AdmissionSelectBatchCard from "./AdmissionSelectBatchCard";
import { useTranslation } from "react-i18next";
const AdmissionSelectBatch = ({ onClose, appId, onSelect, selectedBatch }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [list, setList] = useState([]);
  const {
    admissionOngoingApplicationReviewBatch,
    admissionOngoingApplicationReviewBatchLoading,
  } = useAdmissionOngoingApplicationReviewBatch({
    appId: appId,
    skip: !appId,
  });

  useEffect(() => {
    if (admissionOngoingApplicationReviewBatch?.length > 0)
      setList(admissionOngoingApplicationReviewBatch);
  }, [admissionOngoingApplicationReviewBatch]);

  const onSearchEvent = (e) => {
    if (e.target.value) {
      setSearch(e.target.value);
      let rectifyData = getFilter(
        admissionOngoingApplicationReviewBatch,
        e.target.value
      );
      setList(rectifyData);
    } else {
      setSearch("");
      setList(admissionOngoingApplicationReviewBatch);
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_batch")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0.3rem",
            }}
          >
            <div className={style.assign_search_container}>
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>
            {list?.map((batch) => (
              <AdmissionSelectBatchCard
                batch={batch}
                onSelect={onSelect}
                selectedBatch={selectedBatch}
              />
            ))}

            {admissionOngoingApplicationReviewBatchLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              !list?.length === 0 && (
                <EmptyMessage
                  title="No batch found related this search."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            ) : (
              !admissionOngoingApplicationReviewBatch?.length === 0 && (
                <EmptyMessage
                  title="No batch."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default AdmissionSelectBatch;

const getFilter = (list, search) => {
  let data = list?.filter((bat) => {
    if (bat?.batchName?.toLowerCase()?.includes(search?.toLowerCase()))
      return bat;
    else return null;
  });
  return data;
};
