import { useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import { useTranslation } from "react-i18next";
import styleAdmission from "../OneOngoingAdmission.module.css";
import { useAdmissionOngoingApplicationReviewAllot } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const AdmissionReviewConfirm = ({
  appId,
  onClose,
  onParentClose,
  state,
  applicant,
  onRefetch,
  isMultiple,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [admissionOngoingApplicationReviewAllot] =
    useAdmissionOngoingApplicationReviewAllot();
  const onAllotment = () => {
    if (appId && state?.cls?._id && applicant) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationReviewAllot({
        applicant: {
          dataList: isMultiple ? applicant : [applicant?.student?._id],
        },
        appId: appId,
        cid: state?.cls?._id,
      })
        .then(() => {
          onRefetch();
          onParentClose();
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("allot_to_class")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0.3rem",
            }}
          >
            <div className={styleAdmission.admission_class_confirm}>
              {isMultiple ? (
                <>
                  {t("alloting")}{" "}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {applicant?.length}
                  </span>{" "}
                  {t("students")} {` `}
                </>
              ) : (
                <>
                  {t("lloting_student")}{" "}
                  <span
                    style={{
                      fontWeight: "600",
                    }}
                  >
                    {` ${applicant?.student?.studentFirstName ?? ""} ${
                      applicant?.student?.studentMiddleName
                        ? applicant?.student?.studentMiddleName
                        : ""
                    } ${applicant?.student?.studentLastName ?? ""} `}
                  </span>
                </>
              )}
              ,{t("to")}{" "}
              <span
                style={{
                  fontWeight: "600",
                }}
              >{` ${state?.cls?.className} - ${state?.cls?.classTitle}`}</span>
            </div>
            <CreateButton label="confirm" onAction={onAllotment} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionReviewConfirm;
