import style from "../OneOngoingAdmission.module.css";
import { imageShowUrl } from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import { useTranslation } from "react-i18next";
const AdmissionMultipleReviewCard = ({ applicant, onSelect, applicantId }) => {
  const { t } = useTranslation();
  return (
    <>
      <div
        className={style.oac_container}
        onClick={() => onSelect(applicant?._id)}
        style={{
          justifyContent: "flex-start",
        }}
      >
        <img
          className={style.department_checkbox}
          src={
            applicantId?.includes(applicant?._id)
              ? `${assestsUserAdmissionUrl}/check.svg`
              : `${assestsUserAdmissionUrl}/uncheck.svg`
          }
          alt="check icon"
        />
        <div className={style.oac_container_inner}>
          <img
            alt="Student Request Avatar"
            src={
              applicant?.studentProfilePhoto
                ? `${imageShowUrl}/${applicant?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.oac_container_inner_info}>
            <h6>{`${applicant?.studentFirstName ?? ""} ${
              applicant?.studentMiddleName ? applicant?.studentMiddleName : ""
            } ${applicant?.studentLastName ?? ""}`}</h6>
            <p>
              {t("form_no_only")}: {applicant?.student?.form_no ?? ""}
            </p>
          </div>
        </div>

        <div className={style.oac_container_contact}>
          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/gender.svg`}
              alt="email icon"
            />
            <p>{applicant?.studentGender}</p>
          </div>

          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/phone.svg`}
              alt="phone icon"
            />
            <p>
              {applicant?.user?.userPhoneNumber
                ? applicant?.user?.userPhoneNumber
                : applicant?.user?.userEmail
                ? applicant?.user?.userEmail
                : applicant?.studentPhoneNumber
                ? applicant?.studentPhoneNumber
                : applicant?.studentParentsPhoneNumber
                ? applicant?.studentParentsPhoneNumber
                : applicant?.studentEmail ?? ""}
            </p>
          </div>
        </div>
      </div>
    </>
  );
};

export default AdmissionMultipleReviewCard;
