import { useState } from "react";
import AdmissionSelectBatch from "./AdmissionSelectBatch";
import AdmissionReviewDivision from "./AdmissionReviewDivision";
import AdmissionReviewConfirm from "./AdmissionReviewConfirm";

const AdmissionReviewConfig = ({
  appId,
  applicant,
  onClose,
  onRefetch,
  isMultiple,
}) => {
  const [step, setStep] = useState(1);
  const [state, setState] = useState({
    batch: "",
    cls: "",
  });

  const onSelectBatch = (val) => {
    setStep(2);
    setState((prev) => ({
      ...prev,
      batch: val,
    }));
  };
  const onSelectCls = (val) => {
    setStep(3);
    setState((prev) => ({
      ...prev,
      cls: val,
    }));
  };
  return step === 1 ? (
    <AdmissionSelectBatch
      appId={appId}
      selectedBatch={state?.batch?._id}
      onClose={onClose}
      onSelect={onSelectBatch}
    />
  ) : step === 2 ? (
    <AdmissionReviewDivision
      appId={appId}
      bid={state.batch?._id}
      selectedId={state?.cls}
      onClose={() => setStep(1)}
      onSelect={onSelectCls}
    />
  ) : step === 3 ? (
    <AdmissionReviewConfirm
      appId={appId}
      state={state}
      onClose={() => setStep(2)}
      applicant={applicant}
      onRefetch={onRefetch}
      isMultiple={isMultiple}
      onParentClose={onClose}
    />
  ) : null;
};

export default AdmissionReviewConfig;
