import { useLocation, useSearchParams } from "react-router-dom";
import { useAdmissionOneOngoingApplication } from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AdmissionApplication from "./AdmissionApplication/AdmissionApplication";
import AdmissionAllottment from "./AdmissionAllottment/AdmissionAllottment";
import AdmissionCancelled from "./AdmissionCancelled/AdmissionCancelled";
import DocumentCollection from "./DocumentCollection/DocumentCollection";
import FeesCollection from "./FeesCollection/FeesCollection";
import AdmissionMultipleConfirm from "./AdmissionConfirm/AdmissionMultipleConfirm";
import AdmissionConfirm from "./AdmissionConfirm/AdmissionConfirm";
import AdmissionMultipleReview from "./AdmissionReview/AdmissionMultipleReview";
import AdmissionReview from "./AdmissionReview/AdmissionReview";

const OneOngoingAdmissionConfig = ({ instituteId, aid, financeId }) => {
  const [searchParams] = useSearchParams();
  const getQuery = useLocation();
  const { admissionOneOngoingApplication } = useAdmissionOneOngoingApplication({
    appId: getQuery.state?.appId,
    skip: !getQuery.state?.appId,
  });
  // console.info(
  //   "admissionOneOngoingApplication",
  //   admissionOneOngoingApplication
  // );
  return (
    <>
      {!searchParams.get("n") ? (
        <AdmissionApplication
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={aid}
        />
      ) : searchParams.get("n") === "document" ? (
        <DocumentCollection
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={aid}
        />
      ) : searchParams.get("n") === "fees" ? (
        <FeesCollection
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={aid}
          financeId={financeId}
        />
      ) : searchParams.get("n") === "confirmed" ? (
        <AdmissionConfirm
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : searchParams.get("n") === "confirmed-multi" ? (
        <AdmissionMultipleConfirm
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : searchParams.get("n") === "review" ? (
        <AdmissionReview
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={aid}
        />
      ) : searchParams.get("n") === "review-multi" ? (
        <AdmissionMultipleReview
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
          aid={aid}
        />
      ) : searchParams.get("n") === "allottment" ? (
        <AdmissionAllottment
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : searchParams.get("n") === "cancelled" ? (
        <AdmissionCancelled
          applicationDetail={admissionOneOngoingApplication}
          instituteId={instituteId}
        />
      ) : null}
    </>
  );
};

export default OneOngoingAdmissionConfig;
