import { useState } from "react";
import {
  useAdmissionOngoingApplicationExport,
  useOneApplicationStudentFormGenerate,
} from "../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import QvipleLoader from "../../../Loader/QvipleLoader";
import {
  assestsNavbarUrl,
  assestsUserAdmissionUrl,
  assestsUserFinanceUrl,
} from "../../../Services/UrlConfig/AssestsBaseUrl";
import AddButtonWrapper from "../../../Ui/Button/AddButtonWrapper";
import style from "../../FinanceManager/FinanceManager.module.css";
import TransferApplication from "./TransferApplication/TransferApplication";
import { useLocation, useNavigate, useParams } from "react-router-dom";
import CreateButton from "../../../Ui/Button/CreateButton";

const OneOngoingSearchWrapper = ({
  applicationDetail,
  isExportAccess,
  isAddAccess,
  isTransferAccess,
  instituteId,
  aid,
  onMultipleSelect,
  onResuffleToggle,
  isGenerateAccess,
  isFull,
  children,
}) => {
  const getQuery = useLocation();
  const params = useParams();
  const navigate = useNavigate();
  const [disabled, setDisabled] = useState(false);
  const [step, setStep] = useState("");
  const [admissionOngoingApplicationExport] =
    useAdmissionOngoingApplicationExport();

  const [oneApplicationStudentFormGenerate] =
    useOneApplicationStudentFormGenerate();
  const onCloseStep = () => {
    setStep("");
  };
  const onTransfer = () => {
    setStep(2);
  };

  const onExportList = () => {
    if (getQuery.state?.appId && isAuthorizedTab[isExportAccess]) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationExport({
        appId: applicationDetail?._id,
        flow: isExportAccess,
      })
        .then(() => {
          let url =
            getQuery.state?.urlOffSet === "Institute"
              ? `/q/${params.username}/institute/member/admission?type=export`
              : `/q/${params.username}/member/staff/admission?type=export`;
          navigate(url, {
            state: getQuery.state,
          });
        })
        .catch({});
    }
  };
  const onJoinStudent = () => {
    if (
      instituteId &&
      getQuery.state?.appId &&
      applicationDetail?.applicationMaster &&
      applicationDetail?.applicationDepartment?._id &&
      applicationDetail?.applicationBatch?._id &&
      aid
    )
      navigate("/q/joining", {
        state: {
          formAs: "STUDENT",
          openAs: "ADMISSION_ADMIN_SIDE",
          instituteId: instituteId,
          appId: getQuery.state?.appId,
          admissionId: aid,
          admissionAmount: [
            applicationDetail?.admissionFee ?? 0,
            applicationDetail?.one_installments?.fees ?? 0,
          ],
          applicationMaster: applicationDetail?.applicationMaster,
          applicationDepartment: applicationDetail?.applicationDepartment?._id,
          batchId: applicationDetail?.applicationBatch?._id,
          departmentId: applicationDetail?.applicationDepartment?._id,
          whichFormShow: "DEPARTMENT",
        },
      });
  };

  const onGenerateForm = () => {
    if (applicationDetail?._id) {
      setDisabled((pre) => !pre);
      oneApplicationStudentFormGenerate({
        aid: applicationDetail?._id,
      })
        .then(() => {
          setDisabled((pre) => !pre);
        })
        .catch({});
    }
  };
  return (
    <>
      <AddButtonWrapper
        customStyle={{
          justifyContent: "space-between",
          marginBottom: "0.7rem",
        }}
      >
        {isFull ? (
          <>
            <div className={style.fm_search_full}>
              <section className={style.fm_search_full_container}>
                <div className={style.fm_search_full_container_inner}>
                  <img
                    className={style.fm_search_full_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  {children}
                </div>
              </section>
            </div>
          </>
        ) : (
          <>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                {children}
              </div>
            </section>
            <div className={style.fm_other_admission}>
              {isAuthorizedTab[isGenerateAccess] &&
                !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                  <>
                    {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                      <CreateButton
                        label="generate_form"
                        customStyle={{
                          margin: "0",
                          padding: "0.35rem 0.5rem",
                        }}
                        onAction={onGenerateForm}
                      />
                    )}
                  </>
                )}
              {isAuthorizedTab[isAddAccess] &&
                !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                  <>
                    {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                      <img
                        src={`${assestsUserAdmissionUrl}/add.svg`}
                        alt="icon"
                        title="Add student"
                        onClick={onJoinStudent}
                      />
                    )}
                  </>
                )}

              {isAuthorizedTab[isExportAccess] && (
                <img
                  src={`${assestsUserFinanceUrl}/export.svg`}
                  alt="icon"
                  title="List Export"
                  onClick={onExportList}
                />
              )}
              {isAuthorizedTab[isTransferAccess] &&
                !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                  <>
                    {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                      <img
                        src={`${assestsUserAdmissionUrl}/transfer.svg`}
                        alt="icon"
                        title="Transfer student"
                        onClick={onTransfer}
                      />
                    )}
                  </>
                )}
              {isAuthorizedMultiple[isExportAccess] &&
                !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                  <>
                    {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                      <img
                        src={`${assestsUserAdmissionUrl}/mutliple.svg`}
                        alt="icon"
                        title="Multiple Select"
                        onClick={onMultipleSelect}
                      />
                    )}
                  </>
                )}
              {isAuthorizedSuffle[isExportAccess] &&
                !isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                  <>
                    {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                      <CreateButton
                        label="resuffle"
                        customStyle={{
                          margin: "0",
                          padding: "0.35rem 0.5rem",
                        }}
                        onAction={onResuffleToggle}
                      />
                    )}
                  </>
                )}
            </div>
          </>
        )}
      </AddButtonWrapper>
      {step === 2 ? (
        <TransferApplication
          onClose={onCloseStep}
          instituteId={instituteId}
          appId={applicationDetail?._id}
          aid={aid}
        />
      ) : null}
      {disabled && <QvipleLoader />}
    </>
  );
};

export default OneOngoingSearchWrapper;

const isAuthorizedTab = {
  APPLICATION: true,
  Review: true,
  Request_Query: true,
  Confirm_Query: true,
  Review_Query: true,
  Allot_Query: true,
  Docs_Query: true,
  Fees_Query: true,
};
const isAuthorizedComplete = {
  isCompleted: true,
};

const isAuthorizedMultiple = {
  Confirm: true,
  Confirm_Query: true,
  Review_Query: true,
};
const isAuthorizedSuffle = {
  Confirm_Query: true,
};
