import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../OngoingAdmission/OngoingAdmission.module.css";
const AdmissionSelectBatchCard = ({ batch, onSelect, selectedBatch }) => {
  const onClick = () => {
    onSelect(batch);
  };
  return (
    <div
      className={
        selectedBatch === batch?._id
          ? `${style.admission_confirm_class_card} ${style.admission_confirm_class_card_active}`
          : style.admission_confirm_class_card
      }
      onClick={onClick}
    >
      <img src={`${assestsUserFinanceUrl}/class.svg`} alt="batch Avatar" />
      <div
        className={style.admission_collect_docs}
        style={{
          marginTop: "0",
        }}
      >
        <h6>{batch?.batchName}</h6>
      </div>
    </div>
  );
};

export default AdmissionSelectBatchCard;
