import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import AdmissionCancelledCard from "./AdmissionCancelledCard";
import { useAdmissionOngoingApplicationCancelled } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import OneOngoingSearchWrapper from "../OneOngoingSearchWrapper";
const AdmissionCancelled = ({ applicationDetail, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationCancelled,
    admissionOngoingApplicationCancelledLoading,
  } = useAdmissionOngoingApplicationCancelled({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationCancelled?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationCancelled?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationCancelled?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionOngoingApplicationCancelledLoading &&
      admissionOngoingApplicationCancelled?.list?.length >= 0
    ) {
      if (e.target.value) {
        setSearchCopy(e.target.value);
      } else {
        setSearchCopy("");
        setSearch("");
      }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper applicationDetail={applicationDetail} />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingSearchWrapper
            applicationDetail={applicationDetail}
            instituteId={instituteId}
            isFull
          >
            <input
              type="text"
              placeholder={t("search_cancelled_student")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingSearchWrapper>

          {admissionOngoingApplicationCancelled?.list?.map((applicant, index) =>
            admissionOngoingApplicationCancelled?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <AdmissionCancelledCard
                  applicant={applicant}
                  instituteId={instituteId}
                />
              </div>
            ) : (
              <AdmissionCancelledCard
                applicant={applicant}
                key={applicant?._id}
                instituteId={instituteId}
              />
            )
          )}
          {admissionOngoingApplicationCancelledLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            admissionOngoingApplicationCancelled?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission cancelled found related this search."
              />
            )
          ) : (
            admissionOngoingApplicationCancelled?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission cancelled list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionCancelled;
