import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import { useAdmissionOngoingApplicationAllReviewList } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import AdmissionReviewConfig from "./AdmissionReviewConfig";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import AdmissionMultipleReviewCard from "./AdmissionMultipleReviewCard";

const AdmissionMultipleReview = ({ applicationDetail, instituteId, aid }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [applicantId, setApplicantId] = useState([]);
  const [step, setStep] = useState("");
  const [list, setList] = useState([]);
  const [all, setAll] = useState(false);

  const {
    admissionOngoingApplicationAllReviewList,
    admissionOngoingApplicationAllReviewListLoading,
    admissionOngoingApplicationAllReviewListRefetch,
  } = useAdmissionOngoingApplicationAllReviewList({
    data: {
      appId: getQuery.state?.appId,
    },
    skip: !getQuery.state?.appId,
  });

  useEffect(() => {
    if (admissionOngoingApplicationAllReviewList?.length) {
      setList(admissionOngoingApplicationAllReviewList);
    }
  }, [admissionOngoingApplicationAllReviewList?.length]);

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let fd = getSearchList(
        admissionOngoingApplicationAllReviewList,
        e.target.value
      );
      setList(fd);
    } else {
      setSearch("");
      setList(admissionOngoingApplicationAllReviewList);
    }
  };

  const onNavigateBack = () => {
    navigate(-1);
  };

  const onSelect = (val) => {
    if (applicantId?.includes(val))
      setApplicantId((prev) => prev?.filter((id) => id !== val));
    else setApplicantId((prev) => [...prev, val]);
  };

  const onSelectAll = () => {
    let arr1 = [];
    for (let val of admissionOngoingApplicationAllReviewList) {
      arr1.push(val?._id);
    }
    setApplicantId(arr1);
    setAll(true);
  };
  const onRemoveAll = () => {
    setApplicantId([]);
    setAll(false);
  };

  const onClose = () => {
    setStep("");
  };

  const onAllottment = () => {
    if (applicantId?.length > 0) setStep(1);
  };

  const onRefetch = () => {
    setAll(false);
    setAll([]);
    setApplicantId([]);
    admissionOngoingApplicationAllReviewListRefetch();
    onNavigateBack();
  };

  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
          isExportAccess="Confirm_Query"
          isTransferAccess="Review"
          aid={aid}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <AddButtonWrapper
            customStyle={{
              justifyContent: "space-between",
              marginBottom: "0.7rem",
            }}
          >
            <section className={style.fm_search_container}>
              <div
                className={style.fm_search_container_inner}
                style={{
                  minWidth: "500px",
                }}
              >
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search_applicants")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "500px",
                  }}
                />
              </div>
            </section>
            <div className={style.fm_flex}>
              {!isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                <AdmissionButton
                  label={all ? "remove_all" : "select_all"}
                  count={applicantId?.length}
                  onAction={all ? onRemoveAll : onSelectAll}
                  customStyle={{
                    padding: "0.5rem 1rem",
                    fontSize: "13px",
                  }}
                />
              )}
              {!isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                <AdmissionButton
                  label="allot_class"
                  onAction={onAllottment}
                  customStyle={{
                    padding: "0.5rem 1rem",
                    fontSize: "13px",
                    opacity: applicantId?.length > 0 ? "1" : "0.5",
                  }}
                />
              )}

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onNavigateBack}
              />
            </div>
          </AddButtonWrapper>
          {list?.map((applicant) => (
            <AdmissionMultipleReviewCard
              applicant={applicant}
              key={applicant?._id}
              applicantId={applicantId}
              onSelect={onSelect}
            />
          ))}
          {admissionOngoingApplicationAllReviewListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants found related this search."
              />
            )
          ) : (
            list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {step === 1 ? (
        <AdmissionReviewConfig
          appId={getQuery.state?.appId}
          applicant={applicantId}
          onClose={onClose}
          onRefetch={onRefetch}
          isMultiple
        />
      ) : null}
    </>
  );
};

export default AdmissionMultipleReview;
const isAuthorizedComplete = {
  isCompleted: true,
};

const getSearchList = (list, search) => {
  let dt = list?.filter((val) => {
    let name = `${val?.student?.studentFirstName} ${val?.student?.studentMiddleName} ${val?.student?.studentLastName}`;
    if (name?.toLowerCase()?.includes(search?.toLowerCase())) {
      return val;
    }
    return null;
  });
  return dt;
};
