import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import AdmissionAllottmentCard from "./AdmissionAllottmentCard";
import { useAdmissionOngoingApplicationAllottment } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import OneOngoingSearchWrapper from "../OneOngoingSearchWrapper";

const AdmissionAllottment = ({ applicationDetail, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationAllottment,
    admissionOngoingApplicationAllottmentLoading,
    admissionOngoingApplicationAllottmentRefetch,
  } = useAdmissionOngoingApplicationAllottment({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationAllottment?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationAllottment?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationAllottment?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionOngoingApplicationAllottmentLoading &&
      admissionOngoingApplicationAllottment?.list?.length >= 0
    ) {
      if (e.target.value) {
        setSearchCopy(e.target.value);
      } else {
        setSearchCopy("");
        setSearch("");
      }
    }

    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    admissionOngoingApplicationAllottmentRefetch();
  }, [admissionOngoingApplicationAllottmentRefetch]);
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          isExportAccess="Allot_Query"
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingSearchWrapper
            applicationDetail={applicationDetail}
            isExportAccess="Allot_Query"
          >
            <input
              type="text"
              placeholder={t("search_allotted_student")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingSearchWrapper>

          {admissionOngoingApplicationAllottment?.list?.map(
            (applicant, index) =>
              admissionOngoingApplicationAllottment?.list?.length ===
              index + 1 ? (
                <div key={applicant?._id} ref={ref}>
                  <AdmissionAllottmentCard
                    applicant={applicant}
                    instituteId={instituteId}
                    appId={getQuery.state?.appId}
                    onRefetch={onRefetch}
                  />
                </div>
              ) : (
                <AdmissionAllottmentCard
                  applicant={applicant}
                  key={applicant?._id}
                  instituteId={instituteId}
                  appId={getQuery.state?.appId}
                  onRefetch={onRefetch}
                />
              )
          )}
          {admissionOngoingApplicationAllottmentLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            admissionOngoingApplicationAllottment?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission allottment found related this search."
              />
            )
          ) : (
            admissionOngoingApplicationAllottment?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission allottment list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default AdmissionAllottment;
