import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../../../Ui/Button/CreateButton";
import { useCallback, useEffect, useState } from "react";
import { useInView } from "react-intersection-observer";
import { useAdmissionOngoingApplicationReview } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import TransferApplicantCard from "./TransferApplicantCard";
import TransferSelectOngoingAppllication from "./TransferSelectOngoingAppllication";
import TransferApplicationConfirm from "./TransferApplicationConfirm";
const TransferApplication = ({ onClose, appId, aid }) => {
  const { t } = useTranslation();
  const [transferApplicantId, setTransferApplicantId] = useState([]);
  const [transferStudentObj, setTransferStudentObj] = useState([]);
  const [step, setStep] = useState(1);
  const [application, setApplication] = useState("");

  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const {
    admissionOngoingApplicationReview,
    admissionOngoingApplicationReviewLoading,
  } = useAdmissionOngoingApplicationReview({
    data: {
      appId: appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationReview?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationReview?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationReview?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  const onSelectStudent = (val) => {
    if (transferApplicantId?.includes(val?._id)) {
      setTransferApplicantId((prev) => prev?.filter((id) => id !== val?._id));
      setTransferStudentObj((pre) =>
        pre?.filter((fd) => fd?.studentId !== val?.student?._id)
      );
    } else {
      let stu = {
        studentId: val?.student?._id,
        mode: val?.payment_status,
        type: val?.install_type,
        price: val?.fee_remain,
      };
      setTransferStudentObj((prev) => [...prev, stu]);
      setTransferApplicantId((prev) => [...prev, val?._id]);
    }
  };
  const onSelectNext = () => {
    if (transferApplicantId?.length > 0) setStep(2);
  };

  const onChooseApplication = (val) => {
    setApplication(val);
    setStep(3);
  };
  // console.info("smkdfnskjhbk", step);
  return (
    <>
      {step === 1 ? (
        <Modal>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>{t("select_confirm_student")}</h6>
                </div>
                <div className={style.modal_container_header}>
                  <CreateButton
                    customStyle={{
                      margin: 0,
                      padding: "0.2rem 0.5rem",
                    }}
                    onAction={onSelectNext}
                    label="select"
                    count={transferApplicantId?.length}
                  />
                  <img
                    src={`${assestsUserFinanceUrl}/close.svg`}
                    alt="close icon"
                    onClick={onClose}
                    className={style.close_icon}
                  />
                </div>
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
                marginBottom: "1rem",
              }}
            >
              <div
                className={style.assign_search_container}
                style={{
                  marginBottom: "1rem",
                }}
              >
                <input
                  type="text"
                  placeholder={t("search")}
                  className={style.assign_search_input}
                  onChange={onSearchEvent}
                  style={{
                    width: "100%",
                  }}
                />
                <img
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                  alt="search icon"
                />
              </div>

              {admissionOngoingApplicationReview?.list?.map(
                (applicant, index) =>
                  admissionOngoingApplicationReview?.list?.length ===
                  index + 1 ? (
                    <div key={applicant?._id} ref={ref}>
                      <TransferApplicantCard
                        applicant={{ student: applicant }}
                        applicantId={transferApplicantId}
                        onSelect={onSelectStudent}
                      />
                    </div>
                  ) : (
                    <TransferApplicantCard
                      applicant={{ student: applicant }}
                      key={applicant?._id}
                      applicantId={transferApplicantId}
                      onSelect={onSelectStudent}
                    />
                  )
              )}
              {admissionOngoingApplicationReviewLoading ? (
                search ? (
                  <QvipleLoader />
                ) : (
                  <Loading />
                )
              ) : search ? (
                admissionOngoingApplicationReview?.list?.length === 0 && (
                  <EmptyMessage
                    customStyleContainer={{
                      marginTop: "1.5rem",
                    }}
                    title="No admission applicants found related this search."
                  />
                )
              ) : (
                admissionOngoingApplicationReview?.list?.length === 0 && (
                  <EmptyMessage
                    customStyleContainer={{
                      marginTop: "1.5rem",
                    }}
                    title="No admission applicants list."
                  />
                )
              )}
            </UserMemberContentWrapper>
          </div>
        </Modal>
      ) : step === 2 ? (
        <TransferSelectOngoingAppllication
          onSelect={onChooseApplication}
          aid={aid}
          onClose={() => setStep(1)}
        />
      ) : step === 3 ? (
        <TransferApplicationConfirm
          transferApplicantId={transferApplicantId}
          transferStudentObj={transferStudentObj}
          appId={appId}
          application={application}
          onClose={() => setStep(2)}
          onParentClose={onClose}
        />
      ) : null}
    </>
  );
};

export default TransferApplication;
