import { useTranslation } from "react-i18next";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../FinanceManager/Income/Income.module.css";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import { useEffect, useState } from "react";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import CreateButton from "../../../../Ui/Button/CreateButton";
import FormInput from "../../../../Ui/Input/FormInput";
import FormCalendarInput from "../../../../Ui/Input/FormCalendarInput";
import {
  slashDateFormat,
  yearMonthDay,
} from "../../../../Utils/Functions/dateUtility";
import PaymentModeType from "../../../PaymentModeType/PaymentModeType";
import {
  existingRequiredField,
  requiredField,
} from "../../../../Validation/functions/checkingRequiredField";
import { useAdmissionOngoingApplicationAllottmentCancel } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
const AdmissionAllottmentCancel = ({
  onClose,
  applicant,
  appId,
  onRefetch,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [state, setState] = useState({
    amount: "",
    mode: "",
    fee_payment_mode: "",
    fee_payment_amount: "",
    transaction_date: "",
    fee_bank_name: "",
    fee_bank_holder: "",
    fee_utr_reference: "",
  });
  const [errorField, setErrorField] = useState("");

  const [totalPayAmount, setTotalPayAmount] = useState(0);

  const [admissionOngoingApplicationAllottmentCancel] =
    useAdmissionOngoingApplicationAllottmentCancel();

  useEffect(() => {
    if (applicant?.student?.paidFeeList?.length > 0) {
      const amout = applicant?.student?.paidFeeList?.filter(
        (val) => val?.appId === appId
      );
      setTotalPayAmount(amout?.[0]?.paidAmount ?? 0);
    }
  }, [applicant?.student?.paidFeeList]);
  const onInputAmount = (e) => {
    setState((pre) => ({
      ...pre,
      amount: +e.target.value,
      fee_payment_amount: +e.target.value,
    }));
  };

  const onInputChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTransactionDate = (val) => {
    setState((prev) => ({
      ...prev,
      transaction_date: slashDateFormat(val),
    }));
  };

  const onModeChange = (val) => {
    setState((prev) => ({
      ...prev,
      mode: val === "Cash" ? "Offline" : "Online",
      fee_payment_mode: val,
    }));
  };

  const onCancelReview = () => {
    let validation = requiredField(state, [
      "mode",
      "fee_payment_amount",
      "fee_bank_name",
      "fee_bank_holder",
      "fee_utr_reference",
    ]);
    if (existingRequiredField(validation)) {
      if (applicant && appId && applicant?.student?.fee_structure?._id) {
        setDisabled((pre) => !pre);
        admissionOngoingApplicationAllottmentCancel({
          sid: applicant?.student?._id,
          appId: appId,
          payment: {
            struct: applicant?.student?.fee_structure?._id,
            amount: state?.amount ?? "",
            mode: state?.mode ?? "",
            fee_payment_mode: state?.fee_payment_mode ?? "",
            fee_payment_amount: state?.fee_payment_amount ?? "",
            transaction_date: yearMonthDay(state.transaction_date) ?? "",
            fee_bank_name: state?.fee_bank_name ?? "",
            fee_bank_holder: state?.fee_bank_holder ?? "",
            fee_utr_reference: state?.fee_utr_reference ?? "",
            remainAmount: applicant?.fee_remain ?? 0,
            classId: applicant?.student?.studentClass ?? null,
          },
        })
          .then(() => {
            onRefetch();
            setDisabled((pre) => !pre);
            onClose();
          })
          .catch({});
      }
    } else {
      setErrorField(validation);
    }
  };

  // console.info("applicant", applicant);
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("cancel_addmission")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            {/* <div className={style.admission_fees_row}>
              <h6>{t("applicable_amount")} :</h6>
              <h6>
                {t("rs_only")}.{" "}
                {applicant?.student?.fee_structure?.applicable_fees ?? ""}
              </h6>
            </div> */}
            <div className={style.admission_fees_row}>
              <h6>{t("fees_paid")} :</h6>
              <h6>
                {t("rs_only")} {totalPayAmount}
              </h6>
            </div>

            <FormInput
              label={`${t("refund_amount")}`}
              placeholder={t("please_enter_refund_amount")}
              name={"amount"}
              value={state.amount}
              type={"tel"}
              onChange={onInputAmount}
              errorShow={errorField["amount"]}
            />
            <PaymentModeType
              paymentHeading={t("refund_payment_type_mode")}
              selectLabel={t("mode_placeholder")}
              onClick={onModeChange}
              errorShow={errorField["fee_payment_mode"]}
              defalutValue={state.fee_payment_mode}
              selectedValue={t("please_select_payment_mode")}
              isAuthority="admissionCollectMode"
            />
            <FormCalendarInput
              label={t("select_date_of_transaction")}
              placeholder={t("please_select_date")}
              name={"transaction_date"}
              value={state.transaction_date}
              onChange={onTransactionDate}
              errorShow={errorField["transaction_date"]}
            />

            {state.fee_payment_mode === "Cash" ? null : (
              <>
                <div
                  className={style.admission_fees_row}
                  style={{
                    margin: 0,
                  }}
                >
                  <h6
                    style={{
                      margin: 0,
                      marginTop: "1rem",
                    }}
                  >
                    {t("paid_to")}
                  </h6>
                </div>
                <FormInput
                  label={t("bank_name_collect")}
                  placeholder={t("please_enter_bank_name")}
                  name={"fee_bank_name"}
                  value={state.fee_bank_name}
                  type={"text"}
                  onChange={onInputChange}
                />
                <FormInput
                  label={t("banke_holder_name")}
                  placeholder={t("please_enter_name_of_bank_holder")}
                  name={"fee_bank_holder"}
                  value={state.fee_bank_holder}
                  type={"text"}
                  onChange={onInputChange}
                />
                <FormInput
                  label={t("cheque_dd_neft")}
                  placeholder={t("please_enter_utr_ref_number")}
                  name={"fee_utr_reference"}
                  value={state.fee_utr_reference}
                  type={"text"}
                  onChange={onInputChange}
                />
              </>
            )}
            <CreateButton label="confirm" onAction={onCancelReview} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionAllottmentCancel;
