import { useCallback, useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useInView } from "react-intersection-observer";
import { useLocation } from "react-router-dom";
import { nextPage } from "../../../../Utils/Functions/nextPage";
// import { customDebounce } from "../../../../Utils/Functions/debounce";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import { useAdmissionOngoingApplicationFees } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import FeesCollectionCard from "./FeesCollectionCard";
import OneOngoingSearchWrapper from "../OneOngoingSearchWrapper";

const FeesCollection = ({ applicationDetail, instituteId, aid, financeId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const [search, setSearch] = useState("");
  const [searchCopy, setSearchCopy] = useState("");

  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationFees,
    admissionOngoingApplicationFeesLoading,
    admissionOngoingApplicationFeesRefetch,
  } = useAdmissionOngoingApplicationFees({
    data: {
      appId: getQuery.state?.appId,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationFees?.list?.length) {
      let obj = nextPage(admissionOngoingApplicationFees?.list?.length);
      setState(() => obj);
    }
  }, [admissionOngoingApplicationFees?.list?.length]);

  // const onDebounce = useCallback(
  //   customDebounce((val) => {
  //     setPage(() => 1);
  //     setSearch(val);
  //   }, 1500),
  //   []
  // );

  const onSearchEvent = (e) => {
    if (
      !admissionOngoingApplicationFeesLoading &&
      admissionOngoingApplicationFees?.list?.length >= 0
    ) {
      if (e.target.value) {
        setSearchCopy(e.target.value);
      } else {
        setSearchCopy("");
        setSearch("");
      }
    }
    // if (e.target.value) onDebounce(e.target.value);
    // else {
    //   setPage(() => 1);
    //   setSearch("");
    // }
  };
  const onCallBindSearch = (e) => {
    if (e.key === "Enter") {
      setPage(() => 1);
      setSearch(searchCopy);
    }
  };
  const onRefetch = useCallback(() => {
    setPage(() => 1);
    admissionOngoingApplicationFeesRefetch();
  }, []);
  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <OneOngoingSearchWrapper
            applicationDetail={applicationDetail}
            instituteId={instituteId}
            isExportAccess="Fees_Query"
            aid={aid}
          >
            <input
              type="text"
              placeholder={t("search_applicants")}
              onChange={onSearchEvent}
              onKeyUp={onCallBindSearch}
              value={searchCopy}
            />
          </OneOngoingSearchWrapper>

          {admissionOngoingApplicationFees?.list?.map((applicant, index) =>
            admissionOngoingApplicationFees?.list?.length === index + 1 ? (
              <div key={applicant?._id} ref={ref}>
                <FeesCollectionCard
                  applicant={applicant}
                  instituteId={instituteId}
                  onRefetch={onRefetch}
                  appId={getQuery.state?.appId}
                  financeId={financeId}
                />
              </div>
            ) : (
              <FeesCollectionCard
                applicant={applicant}
                key={applicant?._id}
                instituteId={instituteId}
                onRefetch={onRefetch}
                appId={getQuery.state?.appId}
                financeId={financeId}
              />
            )
          )}

          {admissionOngoingApplicationFeesLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <>{page === 1 ? <QvipleLoader /> : <Loading />}</>
            )
          ) : search ? (
            admissionOngoingApplicationFees?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants found related this search."
              />
            )
          ) : (
            admissionOngoingApplicationFees?.list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
    </>
  );
};

export default FeesCollection;
