import { useLocation, useNavigate, useParams } from "react-router-dom";
import style from "../OneOngoingAdmission.module.css";
import { useCallback, useState } from "react";
import {
  imageShowUrl,
  pdfCdnUrl,
} from "../../../../Services/UrlConfig/BaseUrl";
import {
  assestsFeedUrl,
  assestsUserAdmissionUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import { useTranslation } from "react-i18next";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import AdmissionApplicationFeeReceipt from "../../../TransactionReceipt/AdmissionApplicationFeeReceipt";
import AdmissionReviewCancel from "./AdmissionReviewCancel";
import AdmissionReviewConfig from "./AdmissionReviewConfig";
const AdmissionReviewCard = ({
  instituteId,
  applicant,
  appId,
  onRefetch,
  isMultiple,
  onSelect,
  applicantId,
}) => {
  const { t } = useTranslation();
  const params = useParams();
  const navigate = useNavigate();
  const getQuery = useLocation();

  const [step, setStep] = useState("");

  const onViewApplicantProfile = useCallback(() => {
    navigate(`/q/${params.username}/student/profile`, {
      state: {
        sid: applicant?.student?._id,
        viewAs: "ADMISSION_ADMIN",
        instituteId: instituteId,
        collectAccess: "ADMISSION_ADMIN",
        editInstallmentControl: "authorized",
        govtAccess: "Other",
        cancelAccess: false,
        editStructureControl: "authorized",
        editGovtNumberContorl: "authorized",
        editGovtInstallmentControl: "authorized",
        isSearchEnable: "notauthorized",

        whichUrl:
          getQuery.state?.urlOffSet === "Institute" ? "INSTITUTE" : null,
      },
    });
  }, [instituteId, applicant?.student?._id]);

  const onViewApplication = (e) => {
    e.stopPropagation();
  };

  const onClose = () => {
    setStep("");
  };

  const onCancelApplication = (e) => {
    e.stopPropagation();
    setStep(2);
  };
  const onAllottment = (e) => {
    e.stopPropagation();
    setStep(1);
  };

  return (
    <>
      <div
        className={style.oac_container}
        onClick={onViewApplicantProfile}
        title="View Profile"
      >
        {isMultiple ? (
          <img
            className={style.department_checkbox}
            src={
              applicantId?.includes(applicant?.student?._id)
                ? `${assestsUserAdmissionUrl}/check.svg`
                : `${assestsUserAdmissionUrl}/uncheck.svg`
            }
            alt="check icon"
            onClick={(e) => onSelect(e, applicant?.student?._id)}
          />
        ) : null}
        <div className={style.oac_container_inner}>
          <img
            alt="Student Request Avatar"
            src={
              applicant?.student?.studentProfilePhoto
                ? `${imageShowUrl}/${applicant?.student?.studentProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
          />
          <div className={style.oac_container_inner_info}>
            <h6>{`${applicant?.student?.studentFirstName ?? ""} ${
              applicant?.student?.studentMiddleName
                ? applicant?.student?.studentMiddleName
                : ""
            } ${applicant?.student?.studentLastName ?? ""}`}</h6>
            <p>
              {t("form_no_only")}: {applicant?.student?.form_no ?? ""}
            </p>
            {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
              <p
                onClick={onCancelApplication}
                style={{
                  color: "#436dfa",
                }}
              >
                {t("cancel_admision")}
              </p>
            )}
          </div>
        </div>

        <div className={style.oac_container_contact}>
          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/gender.svg`}
              alt="email icon"
            />
            <p>{applicant?.student?.studentGender}</p>
          </div>

          <div className={style.oac_container_contact_inner}>
            <img
              src={`${assestsUserAdmissionUrl}/phone.svg`}
              alt="phone icon"
            />
            <p>
              {applicant?.student?.user?.userPhoneNumber
                ? applicant?.student?.user?.userPhoneNumber
                : applicant?.student?.user?.userEmail
                ? applicant?.student?.user?.userEmail
                : applicant?.student?.studentPhoneNumber
                ? applicant?.student?.studentPhoneNumber
                : applicant?.student?.studentParentsPhoneNumber
                ? applicant?.student?.studentParentsPhoneNumber
                : applicant?.student?.studentEmail ?? ""}
            </p>
          </div>
        </div>

        <div
          className={style.oac_container_stuff}
          style={{
            width: "25%",
          }}
        >
          <a
            href={`${pdfCdnUrl}/${applicant?.student?.application_print?.[0]?.value}`}
            target="_blank"
            rel="noopener noreferrer"
            style={{
              textDecoration: "none",
              marginTop: "-3px",
            }}
            onClick={onViewApplication}
          >
            <img
              src={`${assestsUserAdmissionUrl}/download.svg`}
              alt="receipt icon"
              title="View Application Form"
            />
          </a>
          {applicant?.student?.fee_receipt?.map((f_r, index) =>
            index > 2 ? (
              ""
            ) : (
              <AdmissionApplicationFeeReceipt
                rid={f_r?._id}
                key={f_r?._id}
                receiptFile={f_r?.receipt_file}
              />
            )
          )}
          {isMultiple ? null : (
            <>
              {getQuery.state?.role === "ONGOING_VIEW_ACCESS" ? null : (
                <AddButtonWrapper>
                  <AdmissionButton
                    label="allot_class"
                    onAction={onAllottment}
                    customStyle={{
                      padding: "0.4rem 0.7rem",
                      fontSize: "13px",
                    }}
                  />
                </AddButtonWrapper>
              )}
            </>
          )}
        </div>
      </div>
      {step === 2 ? (
        <AdmissionReviewCancel
          appId={appId}
          applicant={applicant}
          onClose={onClose}
          onRefetch={onRefetch}
        />
      ) : step === 1 ? (
        <AdmissionReviewConfig
          appId={appId}
          applicant={applicant}
          onClose={onClose}
          onRefetch={onRefetch}
        />
      ) : null}
    </>
  );
};

export default AdmissionReviewCard;
