import React, { useState } from "react";
import CreateButton from "../../../../Ui/Button/CreateButton";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import style from "../../../FinanceManager/Income/Income.module.css";
import { useAdmissionTransferApplicatToOtherApplication } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import styleAdmission from "../../OneOngoingAdmission/OneOngoingAdmission.module.css";
import { useTranslation } from "react-i18next";

const TransferApplicationConfirm = ({
  transferStudentObj,
  application,
  appId,
  transferApplicantId,
  onClose,
  onParentClose,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [admissionTransferApplicatToOtherApplication] =
    useAdmissionTransferApplicatToOtherApplication();
  const onTransferApplicant = () => {
    if (appId && application?._id && transferStudentObj?.length > 0) {
      setDisabled((pre) => !pre);
      admissionTransferApplicatToOtherApplication({
        appId: application?._id,
        transferApplicantData: {
          app_array: transferApplicantId,
          oaid: appId,
          student_array: transferStudentObj,
        },
      })
        .then(() => {
          setDisabled((pre) => !pre);
          onParentClose();
        })
        .catch({});
    }
  };
  return (
    <>
      <Modal>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("select_confirm_student")}</h6>
              </div>

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
                className={style.close_icon}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0",
            }}
          >
            <div className={styleAdmission.admission_class_confirm}>
              {t("transfering")}{" "}
              <span
                style={{
                  fontWeight: "600",
                }}
              >
                {transferStudentObj?.length}
              </span>{" "}
              {t("students")} {` `},{t("to")}{" "}
              <span
                style={{
                  fontWeight: "600",
                }}
              >
                {application?.applicationName ?? ""}
              </span>
            </div>
            <CreateButton label="confirm" onAction={onTransferApplicant} />
          </UserMemberContentWrapper>
        </div>
      </Modal>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default TransferApplicationConfirm;
