import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useLocation, useNavigate } from "react-router-dom";
import OneOngoingAdmissionWrapper from "../OneOngoingAdmissionWrapper";
import UserMemberContainerWrapper from "../../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import Loading from "../../../../Loader/Loading";
import {
  useAdmissionOngoingApplicationAllConfirmList,
  useAdmissionOngoingApplicationConfirmReview,
} from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import AddButtonWrapper from "../../../../Ui/Button/AddButtonWrapper";
import AdmissionButton from "../../../../Ui/Button/AdmissionButton";
import { assestsUserFinanceUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import style from "../../../FinanceManager/FinanceManager.module.css";
import { assestsNavbarUrl } from "../../../../Services/UrlConfig/AssestsBaseUrl";
import AdmissionMultipleConfirmCard from "./AdmissionMultipleConfirmCard";

const AdmissionMultipleConfirm = ({ applicationDetail, instituteId }) => {
  const { t } = useTranslation();
  const getQuery = useLocation();
  const navigate = useNavigate();
  const [search, setSearch] = useState("");
  const [applicantId, setApplicantId] = useState([]);
  const [applicantObj, setApplicantObj] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const [list, setList] = useState([]);
  const [all, setAll] = useState(false);
  const [admissionOngoingApplicationConfirmReview] =
    useAdmissionOngoingApplicationConfirmReview();
  const {
    admissionOngoingApplicationAllConfirmList,
    admissionOngoingApplicationAllConfirmListLoading,
    admissionOngoingApplicationAllConfirmListRefetch,
  } = useAdmissionOngoingApplicationAllConfirmList({
    data: {
      appId: getQuery.state?.appId,
    },
    skip: !getQuery.state?.appId,
  });
  useEffect(() => {
    if (admissionOngoingApplicationAllConfirmList?.length) {
      setList(admissionOngoingApplicationAllConfirmList);
    }
  }, [admissionOngoingApplicationAllConfirmList?.length]);

  const onSearchEvent = (e) => {
    if (e.target.value) {
      let fd = getSearchList(
        admissionOngoingApplicationAllConfirmList,
        e.target.value
      );
      setList(fd);
    } else {
      setSearch("");
      setList(admissionOngoingApplicationAllConfirmList);
    }
  };

  const onNavigateBack = () => {
    navigate(-1);
  };

  const onReview = () => {
    if (applicantId?.length > 0 && getQuery.state?.appId) {
      setDisabled((pre) => !pre);
      admissionOngoingApplicationConfirmReview({
        appId: getQuery.state?.appId,
        review: {
          student_arr: applicantObj,
        },
      })
        .then(() => {
          admissionOngoingApplicationAllConfirmListRefetch();
          setDisabled((pre) => !pre);
          setApplicantId([]);
          setApplicantObj([]);
          setAll(false);
          onNavigateBack();
        })
        .catch({});
    }
  };

  const onSelect = (val, cardId) => {
    if (applicantId?.includes(val)) {
      setApplicantId((prev) => prev?.filter((id) => id !== val));
      setApplicantObj((prev) => prev?.filter((id) => id?.sid !== val));
    } else {
      setApplicantId((prev) => [...prev, val]);
      setApplicantObj((prev) => [
        ...prev,
        {
          sid: val,
          cid: cardId,
        },
      ]);
    }
  };

  const onSelectAll = () => {
    let arr1 = [];
    let arr2 = [];
    for (let val of admissionOngoingApplicationAllConfirmList) {
      arr1.push(val?.student?._id);
      arr2.push({
        sid: val?.student?._id,
        cid: val?._id,
      });
    }
    setApplicantId(arr1);
    setApplicantObj(arr2);
    setAll(true);
  };
  const onRemoveAll = () => {
    setApplicantId([]);
    setApplicantObj([]);
    setAll(false);
  };
  // console.info("applicantObj", applicantObj);

  return (
    <>
      <UserMemberContainerWrapper>
        <OneOngoingAdmissionWrapper
          applicationDetail={applicationDetail}
          instituteId={instituteId}
        />

        <UserMemberContentWrapper
          customStyle={{
            paddingTop: "0.2rem",
          }}
        >
          <AddButtonWrapper
            customStyle={{
              justifyContent: "space-between",
              marginBottom: "0.7rem",
            }}
          >
            <section className={style.fm_search_container}>
              <div
                className={style.fm_search_container_inner}
                style={{
                  minWidth: "500px",
                }}
              >
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search_applicants")}
                  onChange={onSearchEvent}
                  style={{
                    minWidth: "500px",
                  }}
                />
              </div>
            </section>
            <div className={style.fm_flex}>
              {!isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                <AdmissionButton
                  label={all ? "remove_all" : "select_all"}
                  count={applicantId?.length}
                  onAction={all ? onRemoveAll : onSelectAll}
                  customStyle={{
                    padding: "0.5rem 1rem",
                    fontSize: "13px",
                  }}
                />
              )}

              {!isAuthorizedComplete[getQuery.state?.isAuthorization] && (
                <AdmissionButton
                  label="review"
                  onAction={onReview}
                  customStyle={{
                    padding: "0.5rem 1rem",
                    fontSize: "13px",
                    opacity: applicantId?.length > 0 ? "1" : "0.5",
                  }}
                />
              )}

              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onNavigateBack}
              />
            </div>
          </AddButtonWrapper>
          {list?.map((applicant) => (
            <AdmissionMultipleConfirmCard
              applicant={applicant}
              key={applicant?._id}
              applicantId={applicantId}
              onSelect={onSelect}
            />
          ))}
          {admissionOngoingApplicationAllConfirmListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants found related this search."
              />
            )
          ) : (
            list?.length === 0 && (
              <EmptyMessage
                customStyleContainer={{
                  marginTop: "1.5rem",
                }}
                title="No admission applicants list."
              />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {disabled && <QvipleLoader />}
    </>
  );
};

export default AdmissionMultipleConfirm;
const isAuthorizedComplete = {
  isCompleted: true,
};

const getSearchList = (list, search) => {
  let dt = list?.filter((val) => {
    let name = `${val?.student?.studentFirstName} ${val?.student?.studentMiddleName} ${val?.student?.studentLastName}`;
    if (name?.toLowerCase()?.includes(search?.toLowerCase())) {
      return val;
    }
    return null;
  });
  return dt;
};
