import { useCallback, useEffect, useState } from "react";
import { useAdmissionOngoingApplicationReviewClasses } from "../../../../Hooks/ApiHooks/AdmissionManager/admission-manager-api";
import {
  assestsNavbarUrl,
  assestsUserFinanceUrl,
} from "../../../../Services/UrlConfig/AssestsBaseUrl";
import BorderBottom from "../../../../Ui/Border/BorderBottom";
import Modal from "../../../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../../../Ui/UserMember/UserMemberContentWrapper";
import style from "../../../PlateformUser/PlateformUser.module.css";
import QvipleLoader from "../../../../Loader/QvipleLoader";
import Loading from "../../../../Loader/Loading";
import EmptyMessage from "../../../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import SelectDivisionsCard from "../../SelectDivisions/SelectDivisionsCard";
import { nextPage } from "../../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
const AdmissionReviewDivision = ({
  onClose,
  appId,
  bid,
  onSelect,
  selectedId,
}) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });

  const {
    admissionOngoingApplicationReviewClasses,
    admissionOngoingApplicationReviewClassesLoading,
  } = useAdmissionOngoingApplicationReviewClasses({
    data: {
      page: page,
      limit: 10,
      search: search,
      appId: appId,
      bid: bid,
    },
    skip: !appId ? !appId : !bid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (admissionOngoingApplicationReviewClasses?.list?.length) {
      let obj = nextPage(
        admissionOngoingApplicationReviewClasses?.list?.length
      );
      setState(() => obj);
    }
  }, [admissionOngoingApplicationReviewClasses?.list?.length]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };
  return (
    <>
      <Modal onClose={onClose}>
        <div className={style.modal_container}>
          <UserMemberContentWrapper>
            <div className={style.modal_container_outer_header}>
              <div className={style.modal_container_header}>
                <h6>{t("allot_class")}</h6>
              </div>
              <img
                src={`${assestsUserFinanceUrl}/close.svg`}
                alt="close icon"
                onClick={onClose}
              />
            </div>
          </UserMemberContentWrapper>
          <BorderBottom />
          <UserMemberContentWrapper
            customStyle={{
              paddingTop: "0.3rem",
            }}
          >
            <div className={style.assign_search_container}>
              <input
                type="text"
                placeholder={t("search")}
                className={style.assign_search_input}
                onChange={onSearchEvent}
                style={{
                  width: "100%",
                }}
              />
              <img
                src={`${assestsNavbarUrl}/navbar-search.svg`}
                alt="search icon"
              />
            </div>

            {admissionOngoingApplicationReviewClasses?.list?.map((cls, index) =>
              admissionOngoingApplicationReviewClasses?.list?.length ===
              index + 1 ? (
                <div key={cls?._id} ref={ref}>
                  <SelectDivisionsCard
                    cls={cls}
                    selectedCls={selectedId}
                    onSelect={onSelect}
                  />
                </div>
              ) : (
                <SelectDivisionsCard
                  cls={cls}
                  key={cls?._id}
                  selectedCls={selectedId}
                  onSelect={onSelect}
                />
              )
            )}
            {admissionOngoingApplicationReviewClassesLoading ? (
              search ? (
                <QvipleLoader />
              ) : (
                <Loading />
              )
            ) : search ? (
              admissionOngoingApplicationReviewClasses?.list?.length === 0 && (
                <EmptyMessage
                  title="No classes found related this search."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            ) : (
              admissionOngoingApplicationReviewClasses?.list?.length === 0 && (
                <EmptyMessage
                  title="No classes."
                  customStyleContainer={{
                    marginTop: "1.5rem",
                  }}
                />
              )
            )}
          </UserMemberContentWrapper>
        </div>
      </Modal>
    </>
  );
};

export default AdmissionReviewDivision;
